import { Component,state,style,animate,transition, trigger, keyframes } from '@angular/core';
import { DataService } from '../app.service';
import { Http, Response, Headers } from '@angular/http';
import { Location } from '@angular/common';
import { Observable } from 'rxjs/Observable';

declare var $:any;
declare interface Task {
  title: string;
  checked: boolean;
}

@Component({
    moduleId: module.id,
    selector: 'user-cmp',
    templateUrl: 'user.component.html',
    animations: [
        trigger('carduserprofile', [
            state('*', style({
                '-ms-transform': 'translate3D(0px, 0px, 0px)',
                '-webkit-transform': 'translate3D(0px, 0px, 0px)',
                '-moz-transform': 'translate3D(0px, 0px, 0px)',
                '-o-transform':'translate3D(0px, 0px, 0px)',
                transform:'translate3D(0px, 0px, 0px)',
                opacity: 1
            })),
            transition('void => *', [
                style({opacity: 0,
                    '-ms-transform': 'translate3D(0px, 150px, 0px)',
                    '-webkit-transform': 'translate3D(0px, 150px, 0px)',
                    '-moz-transform': 'translate3D(0px, 150px, 0px)',
                    '-o-transform':'translate3D(0px, 150px, 0px)',
                    transform:'translate3D(0px, 150px, 0px)',
                }),
                animate('0.3s 0s ease-out'),
            ])
        ]),
        trigger('cardprofile', [
            state('*', style({
                '-ms-transform': 'translate3D(0px, 0px, 0px)',
                '-webkit-transform': 'translate3D(0px, 0px, 0px)',
                '-moz-transform': 'translate3D(0px, 0px, 0px)',
                '-o-transform':'translate3D(0px, 0px, 0px)',
                transform:'translate3D(0px, 0px, 0px)',
                opacity: 1})),
                transition('void => *', [
                    style({opacity: 0,
                        '-ms-transform': 'translate3D(0px, 150px, 0px)',
                        '-webkit-transform': 'translate3D(0px, 150px, 0px)',
                        '-moz-transform': 'translate3D(0px, 150px, 0px)',
                        '-o-transform':'translate3D(0px, 150px, 0px)',
                        transform:'translate3D(0px, 150px, 0px)',
                    }),
                    animate('0.3s 0.25s ease-out')
                ])
            ])
        ],
    providers: [ DataService ]
    })

export class UserComponent{

        public fullname;
        public occupation;
        public role;
        public payNumber;
        public idNumber;
        public username;
        public address;
        public mobileno;
        public email;
        public region;
        public realm;
        public datecaptured;
        public ImageUpload;
        public regionName;
        public suburbName;
        public id;
        public suburb;
        public regionId;
        public suburbId;
        //get the site url
        public siteurl = window.location.origin;


         constructor( private dataService: DataService, private location: Location)
           {
              this.getCurrentUsers();
              this.getRegion();
              this.getSuburbs();
           }
          ngOnInit(){
            $("#regid").hide();
            $("#subid").hide();

             // password and confrim password validation
                 $('#password, #confirmPassword').on('keyup', function () {
                 if ($('#password').val() == $('#confirmPassword').val()) {
                   $('#divCheckPasswordMatch').html('Passwords match.').css('color', 'green');
                    // $('button[type=update]').attr('enabled', 'enabled');
                 } else
                    $('#divCheckPasswordMatch').html('Sorry, passwords do not match. Please review.').css('color', 'red');
                      // $('button[type=update]').attr('disabled', 'disabled');
                    // $('#confirmPassword').val('');
                    // $('#confirmPassword').focus();
                });
          }

        getCurrentUsers()
             {
                let data = this.dataService.getUserDetails().subscribe(( response: Response) => {

                const str = response.json();
                console.log("current user: ",str)

                this.fullname = str.fullname;
                this.occupation = str.occupation;
                this.payNumber = str.payNumber;
                this.idNumber = str.idNumber;
                this.username = str.username;
                this.mobileno = str.mobileno;
                this.address = str.address;
                this.email = str.email;
                this.region = str.user_region.name;

                // this.realm = str.realm;
                this.datecaptured = str.datecaptured;
                // this.ImageUpload = str.images.imageString;
                this.id = str.id;
                this.regionId = str.region;

                 this.ImageUpload = str.images;
              // console.log("image",  this.image );
                 if(this.ImageUpload  == undefined){
                     this.ImageUpload  ="../src/assets/img/image_placeholder.jpg";
                  }else{
                    this.ImageUpload  = str.images.imageString;
                 }

                 this.suburb = str.user_suburb;
                 this.suburbId = str.suburb;
                 if(this.suburb  == undefined){
                     $("#subs").hide();
                  }else{
                    this.suburb = str.user_suburb.name;
                 }

                 this.role = str.user_role;
                 if(this.role  == undefined){
                     this.role  = "No role added";
                  }else{
                    this.role = str.user_role.name;
                    localStorage.setItem("USER_ROLE",str.user_role.name);
                 }

              });

            }

        ngAfterViewInit(){
            //  Activate the tooltips
            $('[rel="tooltip"]').tooltip();
            $('#region').change(
                function() {
                    //$('#regionId').val() = ;
                     var regid = $('#region option:selected').val();
                     var regname = $('#region option:selected').text();
                     $('#regid').val(regid);
                     $('#regname').text(regname);

                });

            $('#suburb').change(
                function() {
                    //$('#regionId').val() = ;
                     var subid = $('#suburb option:selected').val();
                     var subname = $('#suburb option:selected').text();
                     $('#subid').val(subid);
                     $('#subname').text(subname);

                });
        }


         //Close form and redirect
    closeUser(){window.location.href = this.siteurl +'/dashboard';}

    public getRegion(){

      let data = this.dataService.getRegion().subscribe(( response: Response) => {

        const str = response.json();
        this.regionName = str;
        console.log("Data 4",this.regionName)

      });
    }
     getSuburbs(){

             this.dataService.getSuburbs().subscribe(
                data =>{
                  this.suburbName = data.json();
                  console.log("Suburbs", this.suburbName);

              });
        }

    updatePassword(){

        var password = $("#confirmPassword").val();

        var formModel = Object.assign({"password":password});

        this.dataService.putUsers(this.id,formModel)
        .subscribe(
              (response: Response)=> {
                alert("My profile updated successfully"),
                  console.log("the response is:",response);
                   $('#password').val('');
                   $('#confirmPassword').val('');
             },
              (error) => {
                 if (error.status == 401) {
                       alert("Authentication reguired");
                  } else if(error.status == 422) {
                         alert("My profile was not updated.");
                       //  this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel)
                  } else{
                         alert("My profile was not updated");
                         //this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel);

                      }
              });
       }


    editUser(){

        // var password = sessionStorage.getItem('password');
        var img = this.ImageUpload;
        if(img  == undefined ){
              img ="../src/assets/img/image_placeholder.jpg";
           }
        var address = $("#address").val();
        var email = $("#email").val();
        var fullname = $("#fullname").val();
        var mobileno = $("#mobileno").val();
        var occupation = $("#occupation").val();
        var suburb = $("#subid").val();
        var username = $("#username").val();
        var region = $("#regid").val();
        // var password = $("#confirmPassword").val();

        var formModel = Object.assign(
            {"address":address},{"email":email},{"fullname":fullname},
            {"idNumber":this.idNumber},{"images":{"imageString":img}},
            {"isDesktopUser":true},{"mobileno":mobileno},{"occupation":occupation},
            {"payNumber":this.payNumber},
            // {"password":password},
            {"suburb":suburb},
            {"username":username},
            {"region":region}
            );

        this.dataService.putUsers(this.id,formModel)
        .subscribe(
              (response: Response)=> {
                alert("My profile updated successfully"),
                  console.log("the response is:",response);
                  //this.clearFormData();
                  //this.closeNotifications();
             },
              (error) => {
                 if (error.status == 401) {
                       alert("Authentication reguired");
                  } else if(error.status == 422) {
                         alert("My profile was not updated.");
                       //  this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel)
                  } else{
                         alert("My profile was not updated");
                         //this.clearFormData();
                         console.log("the error is:",error);
                         console.log("Form Data", formModel);

                      }
              });
       }

       //image upload function
    handleFileSelect(evt){
          var files = evt.target.files;
          var file = files[0];

        if (files && file) {
            var reader = new FileReader();

            reader.onload =this._handleReaderLoaded.bind(this);

           // reader.readAsBinaryString(file);
             reader.readAsDataURL(file);
        }
      }

      _handleReaderLoaded(readerEvt) {
         var binaryString = readerEvt.target.result;
                //this.ImageUpload= btoa(binaryString);
                this.ImageUpload= binaryString;
               console.log("image: "+binaryString);
        }

}



